import React, {useState} from "react";
import {useQuery, useSubscription} from '@apollo/react-hooks';
import {gql} from 'apollo-boost';
import {makeStyles} from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import {Grid,Button,ListItemSecondaryAction} from "@material-ui/core";
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import {Link} from "react-router-dom";

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxWidth: 700,
        margin: "50px auto",
        backgroundColor: theme.palette.background.paper,
    },
}));

const POLLS = gql`
    {

        polls{
            name
            id
        }

    }
`;

const POLL_SUBSCRIPTION = gql`
    subscription {
        pollAdded {
            id
            name
        }
    }
`;

export default function HomePage() {
    const {loading, error, data} = useQuery(POLLS);
    const [newPolls, setNewPolls] = useState([]);

    const data2 = useSubscription(
        POLL_SUBSCRIPTION,
        {
            onSubscriptionData: ({subscriptionData: {data: {pollAdded}}}) => {
                console.log(pollAdded);
                const n = [...newPolls].concat(pollAdded)
                setNewPolls(n);
            }
        }
    );

    const classes = useStyles();


    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error :(</p>;

    return (
        <Grid>
            <List component="nav" className={classes.root} aria-label="mailbox folders">

                {
                    data.polls.map((poll) => <React.Fragment><ListItem >
                        <ListItemText primary={poll.name}/>
                        <ListItemSecondaryAction>
                            <Button component={Link}
                                    to={`/fill/${poll.id}`}>پر کردن نظرسنجی</Button>
                            <Button component={Link}
                                    to={`/show/${poll.id}`}>مشاهده نتایج</Button>
                        </ListItemSecondaryAction>
                    </ListItem>
                        <Divider/>
                    </React.Fragment>)
                }
                {
                    newPolls.map((poll) => <React.Fragment><ListItem
                        >
                        <ListItemText  primary={poll.name}/>
                        <ListItemSecondaryAction>
                            <Button component={Link}
                                    to={`/fill/${poll.id}`}>پر کردن نظرسنجی</Button>
                            <Button component={Link}
                                    to={`/show/${poll.id}`}>مشاهده نتایج</Button>
                        </ListItemSecondaryAction>
                    </ListItem>
                        <Divider/>
                    </React.Fragment>)
                }

                {/*<Divider light />*/}
            </List>

        </Grid>
    )
}