import React, {useState} from "react";
import {useMutation} from '@apollo/react-hooks';
import {gql} from 'apollo-boost';
import {Card, Grid, Button, Typography} from "@material-ui/core";
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';

import TextField from "@material-ui/core/TextField";
import {useHistory} from "react-router-dom";
import {makeStyles} from "@material-ui/core/styles";


const useStyles = makeStyles((theme) => ({
    root: {
        width: "90vw",
        margin: "0 auto",

    },
    questions: {
        width: "80%",
        margin: "0 auto",

    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        // flexGrow: 1,
        width: "10vw"
    },
}));

const ADD_POLL = gql`
    mutation addPoll($name: String!,$questions:[QuestionIn!]!) {
        addPoll(name: $name,questions:$questions) {
            id
            name
        }
    }
`;


export default function HomePage() {
    const h = useHistory();
    const classes = useStyles();
    const [questions, setQuestions] = useState([{
        text: "",
        choice_a: "",
        choice_b: "",
        choice_c: "",
        choice_d: "",
    }]);
    const [addPoll, dat] = useMutation(ADD_POLL);


    const [name, setName] = useState([""]);
    const handleName = event => {
        setName(event.target.value);
    };


    const addQuestion = (e) => {
        setQuestions(questions.concat({
            text: "",
            choice_a: "",
            choice_b: "",
            choice_c: "",
            choice_d: "",
        }));
        console.log(questions);
    };
    const handleChange = (e) => {
        const index = parseInt(e.target.name);
        let newQuestions = [...questions];

        newQuestions[index].text = e.target.value;
        setQuestions(newQuestions);
    };
    const handleChangeChoices = (e) => {
        const inf = e.target.name.split('-');
        const index = parseInt(inf[0]);
        const choice = inf[1];
        let newQuestions = [...questions];
        newQuestions[index][choice] = e.target.value;
        setQuestions(newQuestions);
        console.log(questions);
    };
    const handleDelete = (i) => {
        questions.splice(i, 1);
        let newQuestions = [...questions];
        setQuestions(newQuestions);
    };

    const handleSubmit = (e) => {
        e.preventDefault()
        addPoll({
            variables: {
                name,
                questions,
            }
        }).then((res) => h.push('/home'))
    };

    return (
        <form className={classes.root} onSubmit={handleSubmit}>
            <Grid style={{height:"30px",margin:"20px"}} container direction={"row"} justify={"space-between"}>
                <TextField  label={"نام نظرسنجی"} value={name} onChange={handleName}/>

                <Button color="primary" style={{backgroundColor:"#0091EA",color:"white",marginLeft:"5%"}}
                    round color={"info"} onClick={(e) => addQuestion(e)}>
                    <Typography>افزودن سوال</Typography>
                </Button>
            </Grid>
            <Grid spacing={6} className={classes.questions} container direction={"column"}>
                {
                    questions.map((question, index) =>
                        <Grid item className={classes.question} container direction={"column"} key={question.id}>
                            <Card>
                                <CardContent>

                                    <TextField
                                        placeholder="عنوان سوال"
                                        style={{width: "100%", marginBottom: "20px"}}
                                        onChange={(e) => handleChange(e)}
                                        name={`${index}`} value={`${question.text}`}
                                    />


                                    <TextField
                                        placeholder="گزینه ۱"
                                        style={{width: "67%",  marginBottom: "10px"}}
                                        onChange={(e) => handleChangeChoices(e)}
                                        name={`${index}-choice_a`} value={`${question.choice_a}`}
                                    /><TextField
                                    placeholder="گزینه ۲"

                                    style={{width: "67%",  marginBottom: "10px"}}
                                    onChange={(e) => handleChangeChoices(e)}
                                    name={`${index}-choice_b`} value={`${question.choice_b}`}
                                /><TextField
                                    placeholder="گزینه ۳"

                                    style={{width: "67%",  marginBottom: "10px"}}
                                    onChange={(e) => handleChangeChoices(e)}
                                    name={`${index}-choice_c`} value={`${question.choice_c}`}
                                /><TextField
                                    placeholder="گزینه ۴"

                                    style={{width: "67%",  marginBottom: "10px"}}
                                    onChange={(e) => handleChangeChoices(e)}
                                    name={`${index}-choice_d`} value={`${question.choice_d}`}
                                />
                                </CardContent>
                                <CardActions>
                                    <Button color={'secondary'} name={`${index}`} round sm
                                            style={{}}
                                            onClick={() => handleDelete(index)}
                                    >حذف سوال</Button>
                                </CardActions>
                            </Card>
                        </Grid>
                    )
                }
                <Button type={"submit"} style={{color:"white",
                    backgroundColor:"#0091EA",
                    marginTop: "40px",
                    width:"30%",
                    margin: "0 auto",

                }}

                >تایید</Button>
            </Grid>
        </form>

    )
}