import React, {useState} from "react";
import {gql} from "apollo-boost";
import {useQuery, useSubscription} from "@apollo/react-hooks";
import {useParams} from "react-router-dom";
import {Grid,Card,CardContent,Typography} from "@material-ui/core";
// import BarChart from 'react-bar-chart';

import BarChart from 'react-bar-chart';




const POLL = gql`
    query Poll($id: String!) {
        poll(id: $id) {
            name
            questions{
                id
                text
                choice_a
                number_a
                choice_b
                number_b
                choice_c
                number_c
                choice_d
                number_d
            }
        }
    }
`;

const POLL_UPDATE = gql`
    subscription poll($id:String!) {
        poll(id:$id){
            questions{
                number_a
                number_b
                number_c
                number_d
            }
        }
    }
`;

export default function PollPage() {
    const {id}=useParams();
    const [nums,setNums]=useState([]);
    const {loading, error, data} = useQuery(POLL, {
        variables: {id},
        onCompleted:(data1 =>{
            console.log(data1.poll.questions);
            setNums(data1.poll.questions);
            console.log(nums)
        })
    });
    const sub = useSubscription(
        POLL_UPDATE,
        {
            variables: { id},
            onSubscriptionData: ({subscriptionData: {data: {poll}}}) => {
                setNums(poll.questions);
                console.log(nums)
            }
        }
    );
    // const dat = [
    //     {text: 'Man', value: 500},
    //     {text: 'Woman', value: 300}
    // ];
    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error :(</p>;

    return <Grid container={true} style={{marginTop:"30px",marginBottom:"50px"}} alignContent={"center"} direction={"column"}>
        {
            data.poll.questions.map((question,i)=><Card style={{width:500,margin:"20px"}} key={question.id}>
                <BarChart ylabel='تعداد'
                          width={400}
                          height={250}
                          margin = {{top: 20, right: 20, bottom: 30, left: 40}}
                          data={[
                              {text:question.choice_a, value:nums[i]? nums[i].number_a : question.number_a},
                              {text:question.choice_b, value:nums[i]? nums[i].number_b : question.number_b},
                              {text:question.choice_c, value:nums[i]? nums[i].number_c : question.number_c},
                              {text:question.choice_d, value:nums[i]? nums[i].number_d : question.number_d}
                          ]}
                          style={{color:"blue"}}
                          />
                <CardContent>
                    <Typography>
                        {question.text}
                    </Typography>

                </CardContent>


            </Card>)
        }
    </Grid>;
}