import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect,
    Link
} from "react-router-dom";
import HomePage from "./HomePage/homePage";
import AddPollPage from "./AddPollPage/AddPollPage";
import './App.css';
import FillPage from "./FillPage/FillPage";
import PollPage from "./PollPage/PollPage";
import {makeStyles} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import {create} from 'jss';
import rtl from 'jss-rtl';
import {StylesProvider, jssPreset} from '@material-ui/core/styles';

// Configure JSS
const jss = create({plugins: [...jssPreset().plugins, rtl()]});
const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        justifyContent: "space-between",
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        // flexGrow: 1,
        width: "10vw"
    },
}));

function App() {
    const classes = useStyles();
    return (
        <Router>
            <div dir={"rtl"} className="App" >
                <StylesProvider jss={jss}>
                    <AppBar position="static" color={"secondary"}>
                        <Toolbar className={classes.root}>
                            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                                <MenuIcon/>
                            </IconButton>
                            <Button component={Link} to={'/home'} className={classes.title}>
                                <Typography style={{color: "white"}} variant="h6">
                                    خانه
                                </Typography>
                            </Button>
                            <Button component={Link} to={'/addpoll'} color="inherit">برگذاری نظرسنجی</Button>
                        </Toolbar>
                    </AppBar>
                    <Switch>
                        <Route exact path={'/'} render={()=><Redirect to='/home'/>}/>
                        <Route path={'/home'} component={HomePage}/>
                        <Route path={'/addpoll'} component={AddPollPage}/>
                        <Route path={'/fill/:id'} component={FillPage}/>
                        <Route path={'/show/:id'} component={PollPage}/>
                    </Switch>
                </StylesProvider>
            </div>
        </Router>

    );
}

export default App;
