import React, {useState} from 'react';
import {Test, QuestionGroup, Question, Option} from 'react-multiple-choice';
import {gql} from "apollo-boost";
import {useMutation, useQuery} from "@apollo/react-hooks";
import {useParams} from "react-router-dom";
import {Button,Grid} from "@material-ui/core";



const POLL = gql`
    query Poll($id: String!) {
        poll(id: $id) {
            name
            questions{
                id
                text
                choice_a
                choice_b
                choice_c
                choice_d
            }
        }
    }
`;

const FILL_POLL = gql`
    mutation fillPoll($id: String!,$choices:[Choices!]!) {
        fillPoll(id: $id,choices:$choices)
    }
`;


export default function FillPage() {
    const {id} = useParams();
    const {loading, error, data} = useQuery(POLL, {
        variables: {id},
    });
    const [addPoll] = useMutation(FILL_POLL);

    const [choices, setChoices] = useState({});
    // console.log(data);
    // console.log(id);
    // console.log(error);
    const handleSubmit=(e)=>{
        e.preventDefault();
        const vals=Object.values(choices);
        if (vals.length === data.poll.questions.length){
            addPoll({
                variables:{
                    id,
                    choices:vals,
                }
            }).then((res)=>console.log(res)).catch((e)=>console.log(e))
        }
        else
            alert("please answer all questions")
    };
    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error :(</p>;
    return (
        <form onSubmit={handleSubmit}>
            <Grid direction={"column"} container>
            <Test style={{width:"60%", margin: "50px auto",}} onOptionSelect={selectedOptions => setChoices(selectedOptions)}>
                {data.poll.questions.map((question,i)=>(
                    <QuestionGroup key={question.id} questionNumber={i}>
                        <Question>{question.text}</Question>
                        <Option value="A">{question.choice_a}</Option>
                        <Option value="B">{question.choice_b}</Option>
                        <Option value="C">{question.choice_c}</Option>
                        <Option value="D">{question.choice_d}</Option>
                    </QuestionGroup>
                ))}
            </Test>
            </Grid>
            <Button color={'primary'} style={{width:"300px",marginTop:"-30px",marginBottom:"200px"}} type={"submit"}>submit</Button>

        </form>
    );
}